import React,{useState, useEffect} from 'react';

import 'bootstrap/dist/css/bootstrap.min.css';


function DataList(){

    const [userList, setUserList] = useState([]);

    const coloumns = [
        {dataField: 'transportType', text: 'Transport Type', sort:true},
        {dataField: 'busno', text: 'Bus Number'},
        {dataField: 'busregd', text: 'Bus Registration'},
        {dataField: 'source', text: 'Source', sort:true},
        {dataField: 'dest', text: 'Destination', sort:true},
        {dataField: 'sourceTime', text: 'Source Time'},
        {dataField: 'destTime', text: 'Destination Time'},
        {dataField: 'price', text: 'Price', sort:true},
        {dataField: 'stops', text: 'Stops'}
    ]

    useEffect(() =>{
        fetch('http://localhost:5000/transport/')
        .then(response => response.json())
        .then(result=> setUserList(result))
        .catch(error => console.log(error));
         
    },[])

    return <div>
       {/*  <BootstrapTable bootstrap4 keyField='transportType' coloumns={coloumns} data={userList} /> */}
       {/*  <table>
            <thead>
                <th>Transport Type</th>
                <th>Bus Number</th>
                <th>Bus Registration:</th>            
                <th>Source</th>
                <th>Destination</th>
                <th>Source Time</th>
                <th>Destination Time</th>
                <th>Price</th>
                <th>Stops</th>
            </thead>
            <tr>
                {
                    userList && userList.length> 0?
                    userList.map(usr =>
                        <tr>
                            <td>{usr.transportType}</td>
                            <td>{usr.busno}</td>
                            <td>{usr.busregd}</td>
                            <td>{usr.source}</td>
                            <td>{usr.dest}</td>
                            <td>{usr.sourceTime}</td>
                            <td>{usr.destTime}</td>
                            <td>{usr.price}</td> 
                            <td>{usr.stops}</td> 
                        </tr>    
                    )
                    :'Loading'
                }
            </tr>
        </table> */}
    </div>
}

export default DataList;