import React, { Component } from 'react';
import axios from 'axios';
import Sidebar from '../sidebar';

export default class EditTransport extends Component {
    constructor(props) {
      super(props); 

      this.onChangeBusno = this.onChangeBusno.bind(this);
      this.onChangeBusRegd = this.onChangeBusRegd.bind(this);
      this.onChangeSource = this.onChangeSource.bind(this);
      this.onChangeDest = this.onChangeDest.bind(this);
      this.onChangeSourceTime = this.onChangeSourceTime.bind(this);
      this.onChangeDestTime = this.onChangeDestTime.bind(this);
      this.onChangePrice = this.onChangePrice.bind(this);
      this.onChangeTransportType = this.onChangeTransportType.bind(this);
      this.onChangeStops = this.onChangeStops.bind(this);
      this.onSubmit = this.onSubmit.bind(this);
  
      this.state = {
            busno : '',
            busregd: '',
            source: '',
            dest: '',
            sourceTime: '',
            destTime: '',
            price: '',
            transportType: '',
            stops: ''
      }
    }
    componentDidMount() {
        axios.get('http://localhost:5000/transport/'+this.props.match.params.id)
          .then(response => {
            this.setState({
                busno: response.data.busno,
                busregd: response.data.busregd,
                source: response.data.source,
                dest: response.data.dest,
                sourceTime: response.data.sourceTime,
                destTime: response.data.destTime,
                price: response.data.price,
                transportType: response.data.transportType,
                stops: response.data.stops
            })   
          })
          .catch(function (error) {
            console.log(error);
          }) 
    
      }
    onChangeBusno(e) {
        this.setState({
            busno: e.target.value
        })
    }
    onChangeBusRegd(e) {
        this.setState({
            busregd: e.target.value
        })
    }
    onChangeSource(e) {
        this.setState({
            source: e.target.value
        })
    }
    onChangeDest(e) {
        this.setState({
            dest: e.target.value
        })
    }
    onChangeSourceTime(e) {
        this.setState({
            sourceTime: e.target.value
        })
    }
    onChangeDestTime(e) {
        this.setState({
            destTime: e.target.value
        })
    }
    onChangePrice(e) {
        this.setState({
            price: e.target.value
        })
    }
    onChangeTransportType(e) {
        this.setState({
            transportType: e.target.value
        })
    }
    onChangeStops(e) {
        this.setState({
            stops: e.target.value
        })
    }

    onSubmit(e) {
        e.preventDefault();
        const transport = {
            busno: this.state.busno,
            busregd: this.state.busregd,
            source: this.state.source,
            dest: this.state.dest,
            sourceTime: this.state.sourceTime,
            destTime: this.state.destTime,
            price: this.state.price,
            transportType: this.state.transportType,
            stops: this.state.stops
          }
      
          console.log(transport);
          axios.post('http://localhost:5000/transport/update/' + this.props.match.params.id, transport)
          .then(res => console.log(res.data));
    
        window.location = '/'; 
    }
    render() {
        return (
            <div className="row">             
                <Sidebar />            
            <div className="col-md-10">
            <div className="container">
          <h3>Create New Transport</h3> 
          <form onSubmit={this.onSubmit}>
              <div className="row">
                  <div className="col-md-4">
                    <div className="form-group"> 
                        <label>Transport Type: </label>
                        <select required
                            className="form-control"
                            value={this.state.transportType}
                            onChange={this.onChangeTransportType}>
                             <option value='Public Transport'>Public Transport</option>
                             <option value='Private Transport'>Private Transport</option>
                        </select>
                    </div>
                  </div>
                  <div className="col-md-4">
                    <div className="form-group"> 
                        <label>Bus Number: </label>
                        <input  type="text"
                            required
                            className="form-control"
                            value={this.state.busno}
                            onChange={this.onChangeBusno}
                            />
                    </div>
                  </div>
                  <div className="col-md-4">
                    <div className="form-group"> 
                            <label>Bus Registration: </label>
                            <input  type="text"
                                required
                                className="form-control"
                                value={this.state.busregd}
                                onChange={this.onChangeBusRegd}
                                />
                        </div>
                    </div>
              </div>
              <div className="row">
                  <div className="col-md-6">
                      <label>Enter starting (source) Stop</label>
                      <input type="text" required className="form-control"
                        value={this.state.source} onChange={this.onChangeSource} />
                  </div>
                  <div className="col-md-6">
                      <label>Enter Dropping (Destination) Stop</label>
                      <input type="text" required className="form-control"
                        value={this.state.dest} onChange={this.onChangeDest} />
                  </div>
              </div>
              <div className="row">
                  <div className="col-md-4">
                      <label>Enter starting (source) Time</label>
                      <input type="text" required className="form-control"
                        value={this.state.sourceTime} onChange={this.onChangeSourceTime} />
                  </div>
                  <div className="col-md-4">
                      <label>Enter Dropping (Destination) Time</label>
                      <input type="text" required className="form-control"
                        value={this.state.destTime} onChange={this.onChangeDestTime} />
                  </div>
                  <div className="col-md-4">
                      <label>Enter Price ₹</label>
                      <input type="text" required className="form-control"
                        value={this.state.price} onChange={this.onChangePrice} />
                  </div>
              </div>
              <div className="row">
                  <div className="col-md-12">
                    <label>Enter Stops</label>
                      <input type="text" required className="form-control"
                        value={this.state.stops} onChange={this.onChangeStops} />
                  </div>
              </div> 
            <div className="form-group" style={{marginTop:"20px"} }>
              <input type="submit" value="Edit Transport Log" className="btn btn-primary" />
            </div>
          </form>
        </div>
        </div>
        </div>
        )
      }

}